.code_container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 60px;
  font-family: "Rubik";
}

.code_card {
  width: 420px;
  height: max-content;

  padding: 70px;
  background: #ffffff;
  box-shadow: 0px 1px 4px rgba(7, 59, 161, 0.1);
  border-radius: 30px;
}

.code_card_img {
  text-align: center;
}

.code_card_titles {
  text-align: center;
}

.code_card_titles h1 {
  padding-top: 30px;
  color: #073ba1;
  font-weight: 500;
  font-size: 24px;
}

.code_card_titles p {
  padding-top: 15px;
  color: #042668;
  font-weight: 400;
  font-size: 16px;
}

.code_card_titles span {
  color: #073ba1;
  font-weight: 500;
}

.code_card_btn {
  width: 280px;
  height: 50px;

  background: #ebf2ff;
  color: #073ba1;
  margin-top: 20px;
  border: none;
  border-radius: 8px;
}

.code_card_btn:hover {
  border: 2px solid #073ba1;
}


