.rating__title {
    font-weight: 500;
font-size: 32px;
line-height: 40px;
text-align: center;
color: #073BA1;
margin-bottom: 60px;
}

.rating__image__info {
    max-width: 430px;
    width: 100%;
}

.rating__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.rating__item {
    display: flex;
    justify-content: space-between;
    align-self:flex-end;
    align-items: center;
    max-width: 580px;
    width: 100%;
    padding: 20px 10px 15px 20px;
    background: #FFFFFF;
box-shadow: 0px 1px 4px rgba(7, 59, 161, 0.1);
border-radius: 16px;
}


.rating__item__title {
    font-weight: 400;
    font-size: 20px;
line-height: 26px;
color: #042668;
margin-bottom: 6px;
}

.rating__item__price {
    font-weight: 500;
font-size: 24px;
line-height: 32px;
color: #073BA1;
}

.rating__info__wrapper {
    display: flex;
    max-width: 100%;
    align-items: center;
    margin-bottom: 32px;
}

.rating__item__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.rating__item__button {
    font-weight: 500;
font-size: 14px;
line-height: 20px;
color: #FFFFFF;
background: #EC3D5A;
border-radius: 40px;
padding: 15px 21px 15px 25px;
border: none;
}